// variables
@blue: #195f88;
@darkblue: #0d4667;
@paleblue: #84bbdb;
@midblue: #4b88ad;
@red: #d9001f;
@brightred: #f60031;

@stack: Arial, Helvetica, Sans-Serif;

@margin: 20px;

.nomalized {
    font-weight: normal;
    font-style: normal;
}

.opacity-mixin(@opacity: 1) {
    @msopacity: @opacity * 100;
    -ms-filter: "progid: DXImageTransform.Microsoft.Alpha(Opacity=@{msopacity})";
	filter: alpha(opacity=@msopacity);
	opacity: @opacity;
}

.transition-mixin(@param) {
    -webkit-transition: @param;
    -moz-transition: @param;
    -o-transition: @param;
    -ms-transition: @param;
    transition: @param;
}

.grid(@index, @columnWidth: 80) when (@index > 0) {
    .grid-@{index} {
        @width: @index * @columnWidth;
        width: ~"@{width}px";
    }
    .grid(@index - 1, @columnWidth);
}
.grid(0, @_) {}

.grid(12);

body, p, div, h1, h2, h3, h4, h5, h6, table, tbody, tr, td, span, blockquote, a, abbr, address, cite, code, img, ul, ol, li, dl, dd, dt, form, label, fieldset, caption, input, textarea, button {
