body {
	background:@blue;
	font-size:14px
}
div#wrap {
	width:980px;
	position:relative;
	margin:0 auto!important
}
div#sidebar {
	width:220px;
	height:100%;
	float:left;
	background:#2c2c2c;
	border-right:1px solid #2e2f2f;
	position:fixed
}
a#logo {
	text-align:center;
	display:block;
	border-bottom:1px solid #353535;
	width:100%;
	position:relative;
	z-index:999;
	opacity:1;
	.transition-mixin(all 300ms);
	padding: 15px 0 9px 0;
}
ul#navigation {
	margin-top:@margin + 5;
	margin-right:-5px;
	position:relative;
	width:220px;
	z-index:999;
	
	li {
    	border:none;
    	background:none;
    	margin:0;
    	padding:0;
    	
    	a {
    	    &:link, &:visited {
            	text-decoration:none;
            	font-family:'OstrichSansBlack', @stack;
            	margin-bottom:1px;
            	width:auto;
            	font-size:20px;
            	text-align:left;
            	padding-left:10px !important;
            	display:block;
            	.transition-mixin(all 200ms);
            	padding:5px 10px 5px 0;
            }
            
            &:hover, &.active {
            	background:#1b1b1b;
            	color:#FFF;
            }
    	}
    }
}

div#container {
	width:730px;
	float:right;
	padding-right:25px;
	left:-5px;
	position:relative;
	padding-top:0;
	background:#619ec2;
}
div.page {
	margin-bottom:5px solid #dbdbdb;
	margin-bottom:0;
	padding-top:20px
}
div.page_content {
	padding:25px 0 40px 25px
}
div#container h3.page_title {
	font-family: 'OstrichSansBlack';
	margin-left:25px;
	font-size:35px;
	color:#fff;
	border-bottom:1px solid #fff;
	text-shadow:1px 1px 2px #185477;
	line-height:2;
	padding:5px 0 0px 0px
}
div#works {
	margin-top:10px;
	overflow:visible!important;
	width:725px;
}
div#works img {
	width:28%;
	min-height:150px;
	position:relative;
	margin-right:26px;
	margin-bottom:18px;
	float:left;
	cursor:pointer;
	.transition-mixin(all 500ms);
	background:#fff;
}
div#works img:hover {
	top:-10px!important;
	opacity:.5!important
}
ul#works_filter {
	margin-top: @margin * 2 !important;
	float:left;
	clear:both;
	margin:15px 0 5px
}
ul#works_filter li {
	border:none;
	background:none;
	float:left;
	padding:0
}
ul#works_filter li a:link, ul#works_filter li a:visited {
	font-size:12px;
	text-decoration:none;
	display:block;
	color:#FFF;
	margin-right:1px;
	padding:2px 10px
}
ul#works_filter li a:hover, ul#works_filter li a.selected {
	background:@darkblue;
	color:#fff;
	opacity:1;
	-webkit-border-radius: 0.2em;
	-moz-border-radius: 0.2em;
	border-radius: 0.2em;
	/* IE hacks */
    zoom: 1;
 *display: inline;
}
.social_icons {
