.one_half {
	float:left;
	width:50%;
	
	> .column_content,
	> div {
	    padding-right:15px;
	}
}
.one_third {
	float:left;
	width:33.33%;
	
	> .column_content,
	> div {
        padding-right:15px;
	}
}
.one_fourth {
	float:left;
	width:25%;
	
	> .column_content,
	> div {
        padding-right:15px;
	}
}

.last > .column_content, .last > div {
	padding-right:0
}
.one_half.last > .column_content, .one_half.last > div {
	padding-left:15px
}
.one_third.first > .column_content, .one_third.first > div {
	padding-right:20px;
	padding-left:0
}
.one_third > .column_content, .one_third > div {
	padding-left:10px;
	padding-right:10px
}
.one_third.last > .column_content, .one_third.last > div {
	padding-left:20px;
	padding-right:0
}
