/* GRID */
.grid {
	border: 1px solid @midblue;
	border-bottom: none;
	border-right: none;
	overflow: hidden;
}
.grid li {
	border-bottom: 1px solid @midblue;
	border-right: 1px solid @midblue;
	float: left;
	overflow: hidden;
	position: relative;
	width: 25%;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	.transition-mixin(all .3s ease-in-out);
	padding-left:10px !important;
}
.ie7 .grid li {
	width: 19.9%;
	padding-left:10px !important;
}
.grid li a {
	display: block;
}
.grid img {
	display: block;
	height: auto;
	border:0px !important;
	width: 100%;
	.opacity-mixin(.65);
	.transition-mixin(all .3s ease-in-out);
	
}
.grid li:hover img {
    .opacity-mixin();
}
